@import "vars";

table {

  page-break-inside: auto;

  //div {
  //  page-break-inside: avoid;
  //}

  /* This is the key */

  tr {
    page-break-inside: avoid;
    page-break-after: auto;
  }

  thead {
    display: table-header-group
  }

  tfoot {
    display: table-footer-group
  }

  &.table {

    margin: 0;
    width: 99.9%;
    border-collapse: collapse;
    font-size: 11pt;

    th {
      text-align: center;
      font-weight: bold;
    }

    th, td {
      padding: 1pt 2pt;
      border: 1pt solid black;
    }

    td {
      letter-spacing: 0.05em;
      vertical-align: top;
    }

    &.table-detail-view {
      width: 50%;
    }

    &.table-borderless {
      border: none;

      tr, td, th {
        border: none;
      }
    }

  }

}


.card {

  margin-top: 6pt;
  padding: 6pt 0;

  .table {
    th, td {
      padding: 5pt 0;
      border: none;
    }

    &.table-grid-view {

      th, td {
        padding: 5pt;
        border: 1pt solid $black;
      }

      thead {
        tr {
          th {
            text-align: center;
          }
        }
      }
    }
  }
}